* {
    box-sizing: border-box;
}

body {
    height: 100vh;
    margin: 0;
    font-family: 'Nunito', Arial;
}

section {
    height: 100vh;
    padding: 10em;
    
    h1 {
        font-size: 3em;
    }

    p {
        font-size: 1.5em;
    }

    &:first-child {
        background: rgb(0, 126, 243);
        color: white;
    }

    &:last-child {
        background: rgb(31, 37,43);
        color:white
    }
}

.custom-p {
    opacity: 0;
    transition: 6s;
}

.scroll-class {
    opacity: 1;
}
